import { memo, useState, useEffect } from 'react'
import { TextArea, Divider, Input, Modal, Toast } from 'antd-mobile'
import http from '../../utils/http'
import { LeftOutline, InformationCircleOutline } from 'antd-mobile-icons'
import { Form, Button, Upload, Space } from 'antd'
import { EditWrapper } from './style'
import { useSelector } from 'react-redux'

//Add的复制，此处没有实现组件复用
function Edit({ navigate }) {
  const momentRes = useSelector((state) => state.home.itemdetail)
  const [fileList, setFileList] = useState([])
  const [ImageUrl, setImageUrl] = useState()
  useEffect(() => {
    setImageUrl(momentRes?.cover)
  }, [momentRes?.cover])

  const getBase64 = (img, callback) => {
    const reader = new FileReader()
    reader.addEventListener('load', () => callback(reader.result))
    reader.readAsDataURL(img)
  }
  const onUpload = async ({ title, content }) => {
    try {
      // 创建一个 FormData 对象
      const addarticle = new FormData()
      //将form表单内容添加进去
      addarticle.append('title', title)
      addarticle.append('content', content)

      // 发送 POST 请求到后端的上传端点
      if (fileList.length !== 0) {
        fileList.forEach((file) => {
          addarticle.append('cover', file)
        })
      } else {
        try {
          const response = await fetch(momentRes.cover)
          const blob = await response.blob()
          const file = new File([blob], 'cover.jpg') // 使用blob创建File对象
          addarticle.append('cover', file)
        } catch (error) {
          console.error('下载封面文件失败:', error)
        }
      }
      const res = await http({
        method: 'patch',
        url: `/moment/my/${momentRes.id}`,
        data: addarticle,
      })
      // 处理后端的响应

      Modal.alert({
        content: '编辑文章成功~',
        closeOnMaskClick: true,
        onConfirm: () => {
          navigate('/home')
        },
      })
    } catch (error) {
      // 处理错误
      console.error('上传失败:', error)
    }
  }
  function compressImage(file, callback) {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = (event) => {
      const img = new Image()
      img.src = event.target.result
      img.onload = () => {
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        const maxWidth = 800 // 设置最大宽度
        const maxHeight = 600 // 设置最大高度
        let width = img.width
        let height = img.height

        if (width > height) {
          if (width > maxWidth) {
            height *= maxWidth / width
            width = maxWidth
          }
        } else {
          if (height > maxHeight) {
            width *= maxHeight / height
            height = maxHeight
          }
        }

        canvas.width = width
        canvas.height = height
        ctx.drawImage(img, 0, 0, width, height)

        // 将压缩后的图片转为 Blob 对象
        canvas.toBlob((blob) => {
          const compressedFile = new File([blob], file.name, {
            type: file.type,
            lastModified: Date.now(),
          })
          callback(compressedFile)
        }, file.type)
      }
    }
  }

  const props = {
    onRemove: (file) => {
      const index = fileList.indexOf(file)
      const newFileList = fileList.slice()
      newFileList.splice(index, 1)
      setFileList(newFileList)
    },
    beforeUpload: (file) => {
      const isImage = file.type.startsWith('image/')
      if (!isImage) {
        Toast.show('只能上传图片文件！')
        return false
      }
      // 直接压缩图片
      compressImage(file, (compressedFile) => {
        getBase64(compressedFile, (url) => {
          setImageUrl(url)
        })
        setFileList([...fileList, compressedFile])
      })
    },
    fileList,
  }

  return (
    <EditWrapper>
      <div className="add">
        <div className="top">
          <div onClick={() => navigate(-1)} className="back">
            <LeftOutline fontSize={20} />
          </div>
          <InformationCircleOutline fontSize={20} />
        </div>
        <div>
          <Form
            onFinish={onUpload}
            initialValues={{
              title: momentRes?.title,
              content: momentRes?.content,
            }}
          >
            <Form.Item name="cover">
              <div>
                <Upload
                  listType="picture-card"
                  showUploadList={false}
                  {...props}
                >
                  <img
                    className="head"
                    src={ImageUrl}
                    style={{ width: 100, height: 100 }}
                    alt="cover"
                  />
                </Upload>
              </div>
            </Form.Item>
            <Form.Item
              name="title"
              rules={[{ required: true, message: '文章标题不能为空！' }]}
            >
              <Input placeholder="请输入文章标题" style={{ width: 400 }} />
            </Form.Item>
            <Divider />
            <Form.Item
              name="content"
              rules={[{ required: true, message: '文章内容不能为空！' }]}
            >
              <TextArea
                placeholder="请输入内容"
                rows={15}
                showCount
                maxLength={3000}
              />
            </Form.Item>
            <Form.Item>
              <Space size="large">
                <Button type="primary" htmlType="submit">
                  提交修改文章
                </Button>
              </Space>
            </Form.Item>
          </Form>
        </div>
      </div>
    </EditWrapper>
  )
}
export default memo(Edit)
